<template>
    <div class="demo">
        <div>
            <h1> {{ message }} </h1>
            <div v-bind:id="dynamicId">Dynamic ID</div>
            <button 
                type="button"
                class="button" 
                :id="isButtonDisabled"
                @click="clickButton()"
            >
                Click Me 
            </button>
        </div>
    </div>
    <div class="demo">
        <div>
            Counter: {{ counter }} <br />
            <button class="button" type="button" v-on:click="increase()">Add</button>
            <button class="button" type="button" v-on:click="reset()">Reset</button>
            <br />
        </div>
    </div>
</template>


<script>

export default {
    data() {
        return {
            dynamicId: 'div01',
            message: 'hello',
            isButtonDisabled: false,
            counter: 0,
        }
    },
    methods: {
        clickButton(){
            alert('hello click')
        },
        increase(){
            this.counter++
        },
        reset(){
            this.counter--
        },
    },
}
</script>

